.main-groups {
    margin: 10px 12px;
    padding: 0;
    list-style: none;
}

.main-group {
    background: #ffffff;
    border-radius: 5px;
    margin: 10px auto;
    width: 80%;
    box-shadow: 0 2px 11px 0 rgba(0,0,0,.1);
}

.main-group__title {
    text-align: center;
    font-family: inherit;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

.main-group__links {
    margin: 0;
    padding: 0 0 10px 0;
    list-style: none;
}

.main-group__footer {
    text-align: right;
    font-weight: bold;
    padding: 0 10px 6px;
    margin: 0;
}

.footer__number {
    color: white;
    background: red;
    border-radius: 50%;
    padding: 2px;
    display: inline-block;
    width: 21px;
    text-align: center;
    height: 22px;
}

.main-group__links li {
    margin: 4px 12px;
    padding: 1.5px 3px;
}

.main-group__links li a:hover,
.main-group__links li a:active {
    color: black;
}
.Form {
    width: 100%;
}

.Form__Row {
    width: 100%;
    margin: 10px auto;
}

.Btn {
    margin-top: 20px;
    height: 40px;
    border: 0;
    font-weight: bold;
    color: white;
    cursor: pointer;
    border-radius: 3px;
    width: 200px;
}

.Btn__Save {
    background-color: #EF5350;
}

.Input {
    padding: 10px;
    border: 0;
    background-color: #EBEBEB;
    border-bottom: 2px solid red;
    outline: none;
    width: 200px;
}

.Input:focus {
    border-bottom: 2px solid purple;
}

.Form__Select {
    height: 40px;
    width: 200px;
}
* {
    box-sizing: border-box;
    font-family: Roboto;
}

.app {
    background: #EBEBEB;
    border-radius: 3px;
    box-sizing: content-box;
    margin: 20px;
}

.Links {
    display: inline-block;
    width: 50%;
    border-right: 4px solid #FFF;
    box-sizing: border-box;
}

.Form {
    display: inline-block;
    width: 50%;
    vertical-align: top;
    box-sizing: border-box;
    text-align: center;
}

.main-title {
    text-align: center;
}
